import React, { useState, useEffect } from 'react';
import styles from '../styles/Login.module.css';
import { registerUser, getCurrentUser } from '../firebaseConfig';
import { useHistory } from 'react-router-dom';
import * as firebase from 'firebase/app';

const RegisterUser = (props) => {

    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [cPassword, setCPassword] = useState('');

    const history = useHistory();
  
    useEffect(() => {
        getCurrentUser().then((user) => {
            if(user) {
                history.push('/Application')
                //setLogUser(user);
                console.log(user);
            } else {
                history.push('/Register')
                console.log('no user back to -> /Register');
            }
            //setBusy(false)
        })
    }, [])

  //writting user to database - adding name & email !!
  function writeUserData(userId, name, email) {
    firebase.database().ref('users/' + userId).set({
      username: name,
      email: email,
    });
  }

  const register = async () => {
    if (password !== cPassword) {
        console.log("Passwords do not match!")
    }
    else if (username.trim() === '' || password.trim() === '') {
        console.log('Username & password are required!')
    }
    else if (email.trim() === '') {
        console.log('Email is required!')
    }
    else {
        const res = await registerUser(username, password)
        //let database = firebase.database(); //
        if(res) {
            console.log("You have registered successfully")
            getCurrentUser().then((user) => {
                
                writeUserData(user.uid, username, email)
                console.log("Database added for user: " + username)
                
                // u are logged in automaticly u can change that with RECIEVE MAIL !!
                history.push('/Login')
            })
            
        }
    }
}

    const onLog = () => {
        history.push("/Login")
        console.log("onLog")
        //setLogged(true);
    }

  return (
    <div className={styles.LoginUser}>
        <div className={styles.InLogin}>
        <div className={styles.Form}>
            <p>Pozdravljeni</p>
            <input
                type='text'
                placeholder='Uporabniško ime...'
                value={username}
                onChange={(e) => setUsername(e.target.value)}
            />
            <input
                type='email'
                placeholder='E-mail...'
                value={email}
                onChange={(e) => setEmail(e.target.value)}
            />
            <input
                type='password'
                placeholder='Geslo...'
                value={password}
                onChange={(e) => setPassword(e.target.value)}
            />
            <input
                type='password'
                placeholder='Ponovi geslo...'
                value={cPassword}
                onChange={(e) => setCPassword(e.target.value)}
            />
            <button 
                onClick={() => register()}
                className={styles.ButtonLogin}
                >Registriraj se</button> 
        </div>
        <p>Imate račun? 
            <button
            className={styles.ButtonRegister}
            onClick={onLog}>Vpiši se</button>
        </p>
    </div>
  </div>
  );
}
  
export default RegisterUser;