import React, { useState, useEffect } from 'react';
import styles from '../../styles/Desktop.module.css';
import Recepti from '../ApplicationContent/Recepti';
import Messenger from '../ApplicationContent/Messenger';
import * as firebase from 'firebase/app';

//let data: any;
const Users = (props) => {
  
  let userId = props.data // getting UID
  //let ind = props.idata // getting index WHY ??
  
  const [showRecepti, setShowRecepti] = useState(false);
  const [showMessages, setShowMessages] = useState(false);
  //const [userUID, setUserUID] = useState();

  useEffect(() => {
    console.log("how many times it turns off FB.DB.off")
    firebase.database().ref('/users/').off("value");
  }, [])

  // removes recepti when u click on new user !!
  useEffect(() => {
    setShowRecepti(false);
    setShowMessages(false);
  }, [userId])

  // when clicking on recepti 
  //let recepti = null;
  //let messages = null;

  const getReceptiHandler = () => {
    //setUserUID(userId)
    setShowMessages(false);
    setShowRecepti(true);
    console.log("Recepti click")
    //console.log("RCUID: " + userUID);
    console.log("RCId: " + userId)
  }
  const getMessagesHandler = () => {
    //setUserUID(userId)
    setShowRecepti(false);
    setShowMessages(true);
    console.log("Messages click")
    //console.log("RCUID: " + userUID);
    console.log("RCId: " + userId)
  }
  // if (showRecepti) {
  //   recepti = (
  //     <Recepti data={userId} />
  //   )
  // }
  // if (showMessages) {
  //   messages = (
  //     <Messenger data={userId} />
  //   )
  // }

  return  (
    <div className={styles.Desktop}>
      <div className={styles.List}>
        {
          // for now all LIST then SEPERATE and put UID INSIDE !!
          // <p>{userId}</p>
        }
        
        <h4>#Meritve</h4>
        <p>Telesna Masa</p>
        <p>Krvni sladkor</p>
        <p>Pritisk</p>
        <h4 onClick={() => getReceptiHandler()}>#Recepti</h4>
        <h4 onClick={() => getMessagesHandler()}>#Sporočila</h4>
      </div>
      {showRecepti ? <Recepti data={userId} /> : null}
      {showMessages ? <Messenger data={userId} /> : null}
    </div>
  ) 
}

export default Users;
